.body{
  background-color: #282c34;
  min-height: 100vh;
  
}

.App-header {
  background-image: url('../../assets/13117741_5145761.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  min-width: 100vh;
}

.Navbar{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
 
  position: sticky; /* Fixe la navbar */
  top: 0; /* Se colle au haut de la fenêtre */
  z-index: 1000; /* S'assure que la navbar reste au-dessus des autres éléments */
}

.Navbar_Onglets{
  font-family: 'Roboto', 'Open Sans', 'Lato', sans-serif;
  font-size: 17px;
  color: #fff;
  margin-left: 15px;
  margin-right: 15px;
  background-color: #282c34;
  border-radius: 5px 5px 5px 5px;
  position: sticky;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
}

.Navbar_Onglets:hover{
  color: #ffffff;
  background-color: #1b1d2200;
  border: 2px solid #c20a0a;
}

.Logo_Name{
  font-family: 'Roboto', 'Open Sans', 'Lato', sans-serif;
  font-size: 17px;
  font-weight: bold;
  color: #fff;
}

.Central_Element{
  display: flex;
  background: linear-gradient(to right, rgba(255, 0, 0, 0.128), rgba(0, 76, 255, 0.139));
  padding: 80px;
  justify-content: center;
  margin: 330px;
  margin-left: 600px;
  margin-right: 600px;
  border-radius: 5px 5px 5px 5px;
}

.Central_Element_Text{
  margin-right: 50px;
}

.Text_Central{
  color:#fff;
  font-family: 'Roboto', 'Open Sans', 'Lato', sans-serif;

  font-size: 50px;
  font-weight: bold;
}



.Central_Element_Button{
  display: flex;
  padding: 20px;
}

.Central_Element_Button_Discover_me{
  background-color: rgb(255, 44, 44);
  align-content: center;
  align-self: center;
  text-align: center;
  font-weight: bold;
  color: #fff;
  font-size: 18px;
  height: 90px;
  width: 170px;
  margin-right: 15px;
  border-radius: 5px 5px 5px 5px;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
}

.Central_Element_Button_Discover_my_work{
  background-color: rgb(44, 108, 255);
  align-content: center;
  align-self: center;
  text-align: center;
  font-weight: bold;
  color: #fff;
  font-size: 18px;
  height: 90px;
  width: 170px;
  border-radius: 5px 5px 5px 5px;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
  
}



.Central_Element_Button_Discover_me:hover{
  background-color: rgba(255, 44, 44, 0);
  color: rgb(255, 44, 44);
  border:2px solid rgb(255, 44, 44);
  
}

.Central_Element_Button_Discover_my_work:hover{
  background-color: rgba(44, 107, 255, 0);
  color: rgb(44, 108, 255);
  border: 2px solid rgb(44, 108, 255);

}




.About_me{
  background-color: #282c34;
  opacity: 0;
  transform: translateY(80px); 
  transition: opacity 1s ease, transform 1s ease; 
}

.About_me.fade-in {
  min-height: 110vh;
  min-width: 100vh;
  background-color: #282c34;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-right: 450px;
  padding-left: 450px;
  opacity: 1;
  transform: translateY(0); /* Rétablir la position d'origine */
}

.About_me_text {
  margin-right: 100px;
  color: #fff;
  font-size: 20px;
  text-decoration: underline;
  overflow: hidden; /* Masque le texte qui déborde */
  height: 500px; /* Ajustez la hauteur selon votre contenu */
  width: 500px;
}

.About_me_image{
  background-color: #282c34;
}

.text-container {
  position: relative; /* Changement de absolute à relative */
  transition: transform 0.5s ease; /* Pour un effet de glissement */
}

.text-item {
  opacity: 0;
  transition: opacity 1s ease, transform 1s ease; /* Transition pour l'animation */
  position: absolute; /* Assure que le texte ne se chevauche pas */
  width: 100%; /* Prend toute la largeur du conteneur */
  top: 0; /* Alignement au haut */
}

.text-item.active {
  opacity: 1;
  transform: translateY(0); /* Position d'origine */
}

.text-item:not(.active) {
  transform: translateY(-100%); /* Déplace le texte hors de la vue */
}

.Les_cartes {
  opacity: 0;
  transform: translateY(80px);
  transition: opacity 1s ease, transform 1s ease;
}

.Les_cartes.visible {
  background-color: #282c34;
  background-image: url('../../assets/13117741_5145761.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  flex-direction: column;
  opacity: 1;
  transform: translateY(0);
}

.Les_cartes_text{
  padding-top: 20px;
  text-align: center;
  color:#fff;
  font-size: 50px;
  font-weight: bold;
  text-decoration: underline;
  
}

.cards{
  
  border-radius: 5px 5px 5px 5px;
  min-height: 70vh;
  padding: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Central_Element_my_works{
  display: flex;
  background: linear-gradient(to right, rgba(255, 0, 0, 0.128), rgba(0, 76, 255, 0.139));
  padding: 50px;
  justify-content: center;
  margin: 50px;
  margin-left: 700px;
  margin-right: 700px;
  margin-bottom: 200px;
  border-radius: 5px 5px 5px 5px;
}

.Text_Central_my_works{
  color:#fff;
  font-family: 'Roboto', 'Open Sans', 'Lato', sans-serif;

  font-size: 20px;
  font-weight: bold;
}


.My_skills{
  opacity: 0;
  transform: translateY(80px);
  transition: opacity 1s ease, transform 1s ease;
}

.My_skills.vue{
  min-height: 110vh;
  min-width: 100vh;
  background-color: #282c34;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-right: 450px;
  padding-left: 450px;
  opacity: 1;
  transform: translateY(0); /* Rétablir la position d'origine */
}























































































































































